"use client";

import { useState, useRef, useEffect } from 'react';
import { FiMessageSquare, FiX, FiSend, FiMinimize2 } from 'react-icons/fi';

interface Message {
  id: string;
  text: string;
  isBot: boolean;
  timestamp: Date;
}

export default function Chatbot() {
  const [isOpen, setIsOpen] = useState(false);
  const [isMinimized, setIsMinimized] = useState(false);
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      text: "Hello! I'm TrustInn Assistant. I can help you with information about our security analysis platform, pricing, tools, and features. How can I assist you today?",
      isBot: true,
      timestamp: new Date()
    }
  ]);
  const [inputMessage, setInputMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const sendMessage = async (message: string) => {
    if (!message.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      text: message,
      isBot: false,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage('');
    setIsLoading(true);

    try {
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: message,
          context: `You are TrustInn Assistant, an AI chatbot for NITMiner Technologies' TrustInn platform. TrustInn is a comprehensive security analysis and code verification platform that integrates 9 NASA-grade verification tools.

Here is the complete product information you should use to answer questions:

${getTrustInnContext()}

Always be helpful, professional, and provide accurate information about TrustInn's features, pricing, tools, and capabilities. If asked about something not related to TrustInn, politely redirect the conversation back to TrustInn services.`
        }),
      });

      const data = await response.json();

      const botMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: data.response || "I'm sorry, I couldn't process your request. Please try again.",
        isBot: true,
        timestamp: new Date()
      };

      setMessages(prev => [...prev, botMessage]);
    } catch (error) {
      console.error('Chat error:', error);
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: "I'm sorry, I'm having trouble connecting right now. Please try again later.",
        isBot: true,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    sendMessage(inputMessage);
  };

  const getTrustInnContext = () => {
    return `# TrustInn - Complete Product Offering & Pricing Document

*Created: February 3, 2026*  
*Organization: NITMiner Technologies Pvt Ltd*

## Executive Summary
TrustInn is an enterprise-grade Security Analysis & Code Verification Platform that democratizes advanced software testing tools. We integrate 9 NASA-grade verification tools into a single, intuitive web interface.

### What TrustInn Offers:
- Single, intuitive web dashboard
- One-click tool execution
- Standardized results format
- Real-time terminal output
- Interactive visualizations
- Instant report generation

### 9 Professional-Grade Analysis Tools:

#### C/C++ Tools (6 tools):
1. **CBMC** - Test case reachability analysis
2. **KLEEMA** - Mutation testing with dynamic symbolic execution
3. **KLEE** - Path exploration & automatic test generation
4. **TX** - Fast path exploration with intelligent pruning
5. **gMCov** - MC/DC coverage for safety-critical code
6. **gMutant** - Mutation quality assessment

#### Java Tools (1 tool):
7. **JBMC** - Assertion verification & condition coverage

#### Python Tools (1 tool):
8. **Python Analyzer** - Condition coverage with fuzzing

#### Blockchain Tools (1 tool):
9. **VeriSol** - Smart contract formal verification

## Pricing Structure:

### Free Trial Plan - ₹0
- 5 free executions per tool
- Access to all 9 tools
- Basic visualizations
- 7-day validity

### Starter Plan - ₹499/month or ₹4,990/year
- 50 monthly executions
- Interactive visualizations
- PDF reports
- Email support

### Professional Plan - ₹2,499/month or ₹24,990/year
- 500 monthly executions
- Batch operations
- Admin dashboard
- 5 team members
- Priority support

### Enterprise Plan - Custom Pricing
- Unlimited executions
- Full API access
- CI/CD integration
- Dedicated support
- White-label capability

## Key Features:
- Interactive Visualizations (pie charts, bar charts)
- Bulk File Processing
- Smart Download (ZIP with all data)
- Secure File Storage
- Admin Analytics Dashboard
- Batch Operations
- Detailed Reports (PDF/HTML)
- API Access
- Dark Mode Interface

## Use Cases:
- Software Developers (code quality assurance)
- QA/Testing Teams (comprehensive coverage)
- Security Researchers (vulnerability analysis)
- Academia (teaching formal verification)
- Embedded Systems (safety certification)
- Smart Contract Developers (blockchain security)
- Startups & Small Teams (enterprise tools at affordable price)
- Enterprise Organizations (scalable security analysis)

## Contact Information:
- Email: sanghu@nitw.ac.in
- Phone: +91-7013306805
- Website: https://trustinn.com
- Sales: sales@trustinn.com
- Support: support@trustinn.com

## Company Details:
NITMiner Technologies Pvt Ltd - NITW Incubated Innovation hub at NIT Warangal`;
  };

  if (!isOpen) {
    return (
      <div className="fixed bottom-6 right-6 z-50">
        <button
          onClick={() => setIsOpen(true)}
          className="bg-blue-600 hover:bg-blue-700 text-white p-4 rounded-full shadow-lg transition-all duration-300 hover:scale-110"
          aria-label="Open chat"
        >
          <FiMessageSquare size={24} />
        </button>
      </div>
    );
  }

  return (
    <div className={`fixed bottom-6 right-6 z-50 transition-all duration-300 ${
      isMinimized ? 'w-80 h-14' : 'w-80 h-96'
    }`}>
      {/* Chat Header */}
      <div className="bg-blue-600 text-white p-4 rounded-t-lg flex items-center justify-between">
        <div className="flex items-center gap-2">
          <FiMessageSquare size={20} />
          <span className="font-semibold">TrustInn Assistant</span>
        </div>
        <div className="flex items-center gap-2">
          <button
            onClick={() => setIsMinimized(!isMinimized)}
            className="hover:bg-blue-700 p-1 rounded transition-colors"
            aria-label={isMinimized ? "Maximize chat" : "Minimize chat"}
          >
            <FiMinimize2 size={16} />
          </button>
          <button
            onClick={() => setIsOpen(false)}
            className="hover:bg-blue-700 p-1 rounded transition-colors"
            aria-label="Close chat"
          >
            <FiX size={16} />
          </button>
        </div>
      </div>

      {/* Chat Body */}
      {!isMinimized && (
        <>
          <div className="bg-white dark:bg-gray-800 h-72 overflow-y-auto p-4 space-y-4">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${message.isBot ? 'justify-start' : 'justify-end'}`}
              >
                <div
                  className={`max-w-xs px-4 py-2 rounded-lg ${
                    message.isBot
                      ? 'bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-white'
                      : 'bg-blue-600 text-white'
                  }`}
                >
                  <p className="text-sm">{message.text}</p>
                  <p className="text-xs opacity-70 mt-1">
                    {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                  </p>
                </div>
              </div>
            ))}
            {isLoading && (
              <div className="flex justify-start">
                <div className="bg-gray-100 dark:bg-gray-700 px-4 py-2 rounded-lg">
                  <div className="flex items-center gap-2">
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
                    <span className="text-sm text-gray-600 dark:text-gray-300">Typing...</span>
                  </div>
                </div>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>

          {/* Chat Input */}
          <div className="bg-gray-50 dark:bg-gray-900 p-4 rounded-b-lg">
            <form onSubmit={handleSubmit} className="flex gap-2">
              <input
                type="text"
                value={inputMessage}
                onChange={(e) => setInputMessage(e.target.value)}
                placeholder="Ask about TrustInn..."
                className="flex-1 px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-gray-800 dark:text-white"
                disabled={isLoading}
              />
              <button
                type="submit"
                disabled={isLoading || !inputMessage.trim()}
                className="bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white p-2 rounded-lg transition-colors"
                aria-label="Send message"
              >
                <FiSend size={16} />
              </button>
            </form>
          </div>
        </>
      )}
    </div>
  );
}